<template>
  <div>
    <mu-appbar title="">
      <mu-icon-button icon='arrow_back' @click="back" slot="left"/>
    </mu-appbar>
    <div class="p10">
      <mu-text-field label="" hintText="" v-model="password.oldPs" fullWidth/>
      <mu-text-field label="" hintText="" v-model="password.newPs" fullWidth/>
      <mu-text-field label="" hintText="" v-model="password.newPs2" fullWidth/>
      <mu-raised-button label="" @click="submit" secondary fullWidth/>
    </div>
  </div>
</template>
<script>
import qs from 'qs'
export default {
  data () {
    return {
      password: {}
    }
  },
  methods: {
    back () {
      this.$router.back()
    },
    submit () {
      if (this.password.newPs !== this.password.newPs2) {
        return window.alert('')
      }
      if (!this.password.oldPs || !this.password.newPs) {
        return window.alert('')
      }
      this.$http.put('/rest/api/user/detail/password?' + qs.stringify(this.password)).then((res) => {
        this.$cookie.set('password', res.data.attributes.password)
        window.alert('')
      })
    }
  }
}
</script>



